.scrollhost::-webkit-scrollbar {
  display: none;
}

.scrollhost-container {
  position: relative;
  height: 100%;
}
.scrollhost {
  overflow: auto;
  height: 100%;
  scrollbar-width: none;
  -ms-overflow-style: none;
  position: relative;
}

.scroll-bar {
  width: 8px;
  height: 100%;
  right: 0;
  top: 0px;
  position: absolute;
  border-radius: 7px;
  bottom: 0px;
  background-color: #0092e0;
}

.scroll-thumb {
  width: 8px;
  /* height: 20px; */
  /* margin-left: 1px; */
  position: absolute;
  border-radius: 7px;
  opacity: 1;
  top: 0;
  background-color: #ffffff;
  z-index: 1000;
}
